<template>
  <div class="login-contain">
    <sub-header headerTitle=""></sub-header>
    <mt-field label="" placeholder="" type="email" v-model="email" required></mt-field>
    <mt-field label="" placeholder="" type="password" v-model="password" required></mt-field>
    <mt-field label="" placeholder="" v-model="captcha" required>
      <img v-bind:src="captchaUrl" height="45px" width="80px" @click="getCaptcha">
    </mt-field>
    <mt-button class="setting-button" size="large" @click.native="handleLogin"></mt-button>
  </div>
</template>

<script>
  import {Button, Field, Toast, Indicator} from 'mint-ui'
  import SubHeader from '../components/SubHeader.vue'
  import qs from 'qs'

  export default {
    components: {
      'sub-header': SubHeader,
      'mt-field': Field,
      'mt-button': Button,
    },
    name: 'Login',
    data() {
      return {
        email: '',
        password: '',
        captcha: '',
        captchaUrl: ''
      }
    },
    mounted: function () {
      this.getCaptcha()
    },
    computed: {},
    methods: {
      getCaptcha: function () {
        var self = this;
        this.$http.get('/get_bilibili_captcha', {}).then(function (response) {
          self.captchaUrl = "data:image/jpeg;base64," + response.data.data.captcha
        })
          .catch(function (error) {
            console.log(error);
            self.captchaUrl = ""
          });

      },
      handleLogin: function () {
        if (this.email === '') {
          return Toast({
            message: '',
            position: 'bottom',
          });
        }
        let emailReg = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/
        if (!emailReg.test(this.email)) {
          return Toast({
            message: '',
            position: 'bottom',
          });
        }
        if (this.password === '') {
          return Toast({
            message: '',
            position: 'bottom',
          });
        }
        if (this.captcha === '') {
          return Toast({
            message: '',
            position: 'bottom',
          });
        }
        Indicator.open({
          text: '...',
          spinnerType: 'fading-circle'
        });
        var self = this;
        this.$http.post('/get_bilibili_cookie',
          qs.stringify({
            user: this.email,
            password: this.password,
            captcha: this.captcha
          }),
          {
            headers: {
              content_type: 'application/x-www-form-urlencoded',
            }
          }).then(function (response) {
          console.log(response);
          Indicator.close();
          if (response.data.success === 1) {
            self.$store.dispatch({
              type: 'UPDATE_COOKIE',
              cookie: response.data.data.cookie
            })
            self.$router.go(-1)
            self.$store.dispatch({
              type: 'START_USER_SERVICE'
            })
            return Toast({
              message: '',
              position: 'bottom',
            })
          } else {
            return Toast({
              message: response.data.msg,
              position: 'bottom',
            })
          }
        })
          .catch(function (error) {
            console.log(error);
            Indicator.close();
          });
      }
    }
  }
</script>

<style scoped>
  .login-contain {
    height: 100%;
  }

  .setting-button {
    margin-top: 10px;
    margin-bottom: 10px;
    border: 1px solid #ffffff;
    background-color: transparent;
    color: #000000;
  }


</style>



