const API_LIVE_BASE_URL = '/apilivebilibilicom'

//  1 ->  0 -> 
export function toggleLiveRoom(status, area) {
  let body = {
    status,
    roomid: this.roomId
  }
  if (area) {
    body.area_v2 = area
  }
  return this.post({
    url: `${API_LIVE_BASE_URL}/live_status_mng`,
    body
  }).then(res => {
    let data = JSON.parse(res)
    return data
  })
}

// 
export function getAreaList() {
  return this.get({
    url: `${API_LIVE_BASE_URL}/room/v1/Area/getList`,
  }).then(res => {
    let data = JSON.parse(res).data
    return data
  })
}

// 
export function getRoomRTMP() {
  return this.post({
    url: `${API_LIVE_BASE_URL}/liveact/getrtmp`,
    body: {
      roomid: this.roomId
    }
  }).then(res => {
    let data = JSON.parse(res)
    if (data.code < 0) return false
    data = data.data
    return {
      address: data.addr,
      code: data.code
    }
  })
}

// 
export function blockUser(userId, hour) {
  return this.post({
    url: `${API_LIVE_BASE_URL}/liveact/room_block_user`,
    body: {
      roomid: this.roomId,
      content: userId,
      type: 1,
      hour: hour
    }
  }).then(res => {
    let data = JSON.parse(res)
    return data
  })
}

// 
export function deleteBlockUser(roomId, blockId) {
  return this.post({
    url: `${API_LIVE_BASE_URL}/liveact/del_room_block_user`,
    body: {
      roomid: roomId,
      id: blockId
    }
  }).then(res => {
    let data = JSON.parse(res)
    return data
  })
}

// 
export function getBlockUserList(roomId, page) {
  return this.get({
    url: `${API_LIVE_BASE_URL}/liveact/ajaxGetBlockList`,
    params: {
      roomid: roomId,
      page: page
    }
  }).then(res => {
    let data = JSON.parse(res)
    if (data.code != 0) return []
    return data
  })
}

// 
export function setAdmin(userId) {
  return this.post({
    url: `${API_LIVE_BASE_URL}/liveact/admin`,
    body: {
      content: userId,
      roomid: this.roomId,
      type: 'add'
    }
  }).then(res => {
    let data = JSON.parse(res)
    return data
  })
}

// 
export function deleteAdmin(userId) {
  return this.post({
    url: `${API_LIVE_BASE_URL}/liveact/admin`,
    body: {
      content: userId,
      roomid: this.roomId,
      type: 'del'
    }
  }).then(res => {
    let data = JSON.parse(res)
    return data
  })
}



